<template>
  <div class="signin--Cntr">
    <div class="signin--bgCntr">
      <div class="signin--fromCntr">
        <img
          src="../../assets/images/shikshanLogo.png"
          alt="shikshanLogo"
          class="shikshanLogo"
        />
        <b-field>
          <b-input
            placeholder="Email"
            v-model="email"
            type="email"
            icon="email"
            icon-right="close-circle"
            icon-right-clickable
            @icon-right-click="clearIconClick"
            required
          >
          </b-input>
        </b-field>
        <b-field>
          <b-input
            type="password"
            placeholder="Password"
            v-model="password"
            icon="lock"
            icon-clickable
            password-reveal
            required
            validation-message="Please enter password"
          >
          </b-input>
        </b-field>
        <div class="errorMsg--Cntr" v-if="errorMsgToShow">
          <p class="errorMsg--txt">{{ errorMsg }}</p>
        </div>
        <div class="btn--Cntr" @click="signinButton_js">
          {{ buttonName }}
        </div>
        <div class="signinWith--Cntr">
          <p class="signinWith--Cntr__txt">Or Login With</p>
          <a href="#">
            <img
              src="../../assets/images/google.png"
              alt="googleLogo"
              class="signinWith--Cntr__Glogo"
            />
          </a>
          <a href="#">
            <img
              src="../../assets/images/facebook.png"
              alt="facebookLogo"
              class="signinWith--Cntr__Flogo"
            />
          </a>
        </div>
        <div class="signupwith--Cntr">
          Don’t Have An Account Yet?
          <span @click="routeTosigninPage">Signup</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Login",
  data() {
    return {
      email: "",
      password: "",
      buttonName: "Log In",
      errorMsgToShow: false,
      errorMsg: ""
    };
  },
  methods: {
    clearIconClick() {
      this.email = "";
    },
    routeTosigninPage() {
      this.$router.push({ name: "Signup" });
    },
    signinButton_js() {
      if (this.email === "" && this.password === "") {
        this.errorMsgToShow = true;
        this.errorMsg = "Please enter your email & password";
      } else if (this.email === "") {
        this.errorMsgToShow = true;
        this.errorMsg = "Please enter your emailID";
      } else if (this.password === "") {
        this.errorMsgToShow = true;
        this.errorMsg = "Please enter your password";
      } else {
        this.errorMsgToShow = false;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@import "./../../assets/scss/_utils.scss";

.signin--Cntr {
  background-image: url("https://i.ibb.co/cLG6spb/background-Theme.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  position: relative;
  .signin--fromCntr {
    position: absolute;
    top: 20%;
    left: 5%;
    background: #fff;
    width: 36%;
    padding: 40px;
    border-radius: 30px;
    @include respond-to(xs) {
      width: 90%;
      top: 16%;
      bottom: 14%;
      padding: 20px;
    }
    .shikshanLogo {
      display: table;
      margin: 0 auto;
      margin-bottom: 30px;
      @include respond-to(xs) {
        margin-bottom: 10px;
      }
    }
    .errorMsg--Cntr {
      display: table;
      margin: 0 auto;
      .errorMsg--txt {
        font-size: 14px;
        line-height: 20px;
        color: #d23056;
        font-weight: bold;
      }
    }
    .btn--Cntr {
      background: #d23056;
      font-weight: bold;
      cursor: pointer;
      color: #fff;
      width: 40%;
      padding: 8px;
      text-align: center;
      border-radius: 22px;
      margin: 0 auto;
      margin-top: 40px;
      -webkit-box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
      -moz-box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
      box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
      &:hover {
        background: #da496b;
      }
    }
    .signinWith--Cntr {
      display: table;
      vertical-align: middle;
      margin: 0 auto;
      margin-top: 20px;
      margin-bottom: 20px;
      .signinWith--Cntr__txt {
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
        font-size: 14px;
        line-height: 20px;
        color: #000;
      }
      .signinWith--Cntr__Glogo {
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
        width: 20px;
      }
      .signinWith--Cntr__Flogo {
        display: inline-block;
        vertical-align: middle;
        width: 24px;
      }
    }
    .signupwith--Cntr {
      display: table;
      margin: 0 auto;
      font-size: 14px;
      line-height: 20px;
      color: #000;
      span {
        color: #da496b;
        cursor: pointer;
      }
    }
  }
}
</style>



