<template>
  <div class="signup--Cntr">
    <div class="signup--bgCntr">
      <div class="signup--fromCntr">
        <img
          src="../../assets/images/shikshanLogo.png"
          alt="shikshanLogo"
          class="shikshanLogo"
        />
        <b-field>
          <b-input
            v-model="name"
            placeholder="Name"
            type="text"
            icon="account"
            icon-clickable
            required
            validation-message="Please enter name"
          ></b-input>
        </b-field>
        <b-field>
          <b-input
            placeholder="Email"
            v-model="email"
            type="email"
            icon="email"
            icon-right="close-circle"
            icon-right-clickable
            @icon-right-click="clearIconClick"
            required
          >
          </b-input>
        </b-field>
        <b-field>
          <b-input
            type="password"
            placeholder="Password"
            v-model="password"
            icon="lock"
            icon-clickable
            password-reveal
            required
            validation-message="Please enter password"
          >
          </b-input>
        </b-field>
        <b-field>
          <b-input
            type="password"
            placeholder="Confirm Password"
            v-model="Confirmpassword"
            icon="lock"
            icon-clickable
            password-reveal
            required
            validation-message="Please confirm your password"
          >
          </b-input>
        </b-field>
        <div class="errorMsg--Cntr" v-if="errorMsgToShow">
          <p class="errorMsg--txt">{{ errorMsg }}</p>
        </div>
        <div class="btn--Cntr" @click="signupButton_js">
          {{ buttonName }}
        </div>
        <div class="signupWith--Cntr">
          <p class="signupWith--Cntr__txt">Sign Up With</p>
          <a href="#">
            <img
              src="../../assets/images/google.png"
              alt="googleLogo"
              class="signupWith--Cntr__Glogo"
            />
          </a>
          <a href="#">
            <img
              src="../../assets/images/facebook.png"
              alt="facebookLogo"
              class="signupWith--Cntr__Flogo"
            />
          </a>
        </div>
        <div class="signInwith--Cntr">
          Already Have An Account <span @click="routeToLoginPage">Login ?</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Signup",
  data() {
    return {
      email: "",
      name: "",
      password: "",
      Confirmpassword: "",
      buttonName: "Sign Up",
      errorMsg: "",
      errorMsgToShow: false
    };
  },
  methods: {
    clearIconClick() {
      this.email = "";
    },
    routeToLoginPage() {
      this.$router.push({ name: "Login" });
    },
    signupButton_js() {
      if (
        this.email === "" &&
        this.name === "" &&
        this.password === "" &&
        this.Confirmpassword === ""
      ) {
        this.errorMsgToShow = true;
        this.errorMsg = "Please enter all the fields";
      } else if (this.email === "") {
        this.errorMsgToShow = true;
        this.errorMsg = "Please enter your emailID";
      } else if (this.name === "") {
        this.errorMsgToShow = true;
        this.errorMsg = "Please enter your name";
      } else if (this.password === "") {
        this.errorMsgToShow = true;
        this.errorMsg = "Please enter password";
      } else if (this.Confirmpassword === "") {
        this.errorMsgToShow = true;
        this.errorMsg = "Please enter your confirm password field";
      } else {
        this.errorMsgToShow = false;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@import "./../../assets/scss/_utils.scss";

.signup--Cntr {
  background-image: url("https://i.ibb.co/cLG6spb/background-Theme.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  position: relative;
  .signup--fromCntr {
    position: absolute;
    top: 16%;
    left: 5%;
    background: #fff;
    width: 36%;
    padding: 40px;
    border-radius: 30px;
    @include respond-to(xs) {
      width: 90%;
      top: 4%;
      bottom: 4%;
      padding: 20px;
    }
    .shikshanLogo {
      display: table;
      margin: 0 auto;
      margin-bottom: 30px;
      @include respond-to(xs) {
        margin-bottom: 10px;
      }
    }
    .errorMsg--Cntr {
      display: table;
      margin: 0 auto;
      .errorMsg--txt {
        font-size: 14px;
        line-height: 20px;
        color: #d23056;
        font-weight: bold;
      }
    }
    .btn--Cntr {
      background: #d23056;
      font-weight: bold;
      cursor: pointer;
      color: #fff;
      width: 40%;
      padding: 8px;
      text-align: center;
      border-radius: 22px;
      margin: 0 auto;
      margin-top: 40px;
      -webkit-box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
      -moz-box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
      box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
      &:hover {
        background: #da496b;
      }
    }
    .signupWith--Cntr {
      display: table;
      vertical-align: middle;
      margin: 0 auto;
      margin-top: 20px;
      margin-bottom: 20px;
      .signupWith--Cntr__txt {
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
        font-size: 14px;
        line-height: 20px;
        color: #000;
      }
      .signupWith--Cntr__Glogo {
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
        width: 20px;
      }
      .signupWith--Cntr__Flogo {
        display: inline-block;
        vertical-align: middle;
        width: 24px;
      }
    }
    .signInwith--Cntr {
      display: table;
      margin: 0 auto;
      font-size: 14px;
      line-height: 20px;
      color: #000;
      span {
        color: #da496b;
        cursor: pointer;
      }
    }
  }
}
</style>



