<template>
  <header>
    <div class="header--Cntr">
      <div class="shikshanLogo--Cntr">
        <img
          src="../../assets/images/shikshanLogo.png"
          alt="shikshanLogo"
          class="shikshanLogo--img"
        />
      </div>
      <div class="miscellenious--Cntr">
        <img
          src="../../assets/images/notificationIcon.png"
          alt="notificationIcon"
          class="notificationIcon--img"
        />
        <img
          src="../../assets/images/ProfilePic.png"
          alt="ProfilePic"
          class="ProfilePic--img"
        />
      </div>
      <div class="signupBtn--cntr" @click="signupPage">
        {{ signupBtnName }}
      </div>
    </div>
  </header>
</template>
<script>
export default {
  name: "headerIndex",
  data() {
    return {
      signupBtnName: "Sign Up"
    };
  },
  methods: {
    signupPage() {
      this.$router.push({ name: "Signup" });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../../assets/scss/_utils.scss";

.header--Cntr {
  background: #fff;
  -webkit-box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
  -moz-box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
  box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
  .shikshanLogo--Cntr {
    display: inline-block;
    vertical-align: middle;
    width: 50%;
    @include respond-to(xs) {
      width: 30%;
    }
    .shikshanLogo--img {
      margin-left: 200px;
      @include respond-to(xs) {
        margin: 0;
        vertical-align: middle;
      }
    }
  }
  .miscellenious--Cntr {
    display: inline-block;
    vertical-align: middle;
    width: 40%;
    text-align: right;
    .notificationIcon--img {
      margin: 10px;
      @include respond-to(xs) {
        margin: 5px;
        width: 20px;
      }
    }
    .ProfilePic--img {
      margin: 10px;
      @include respond-to(xs) {
        margin: 5px;
        width: 25px;
      }
    }
  }
  .signupBtn--cntr {
    display: inline-block;
    vertical-align: middle;
    background: -webkit-linear-gradient(top left, #a9096c, #f04e47);
    background: -moz-linear-gradient(top left, #a9096c, #f04e47);
    background: linear-gradient(to bottom right, #a9096c, #f04e47);
    width: 10%;
    text-align: center;
    color: #fff;
    padding: 10px;
    border-radius: 20px;
    cursor: pointer;
    -webkit-box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
    -moz-box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
    box-shadow: 0px 3px 9px 1px rgba(0, 0, 0, 0.23);
    &:hover {
      background: #f04e47;
    }
    @include respond-to(xs) {
      width: 22%;
      padding: 4px;
      font-size: 12px;
    }
  }
}
</style>



