import Vue from "vue";
import Router from "vue-router";
import Shikshan from "@/components/shikshan";
import Signup from "@/components/signup/index.vue";
import Login from "@/components/login/index.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Shikshan",
      component: Shikshan
    },
    {
      path: "/Signup",
      name: "Signup",
      component: Signup
    },
    {
      path: "/Login",
      name: "Login",
      component: Login
    }
  ]
});



